
































































































































import uniIcon from "@/components/uni-icon/uni-icon.vue"
import uniCollapse from '@/components/uni-collapse/uni-collapse.vue'
import uniCollapseItem from '@/components/uni-collapse-item/uni-collapse-item.vue'
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default {
	components: {uniIcon,uniCollapse,uniCollapseItem,uniList,uniListItem,uniPopup},
	data() {
		return {
			el: 'cdcd',
			addressActive:false,  //修改地址显示与隐藏
			delivery:false,  //配送方式
			payment:false,  //支付方式
			addressInfo: true,  //收货地址
			addcheckNum: 0,  //地址下标
			dis:"none",
			title: 'radio',
			items: [
				{
					value: 'no',
					name: '不开发票',
					checked: 'true'
				},
				{
					value: 'specialPurpose',
					name: '增值税专用发票'
				},
				{
					value: 'ordinary',
					name: '增值税普通发票'
				},
			],
			invoices: [{
					value: '1',
					name: '企业',
					checked:'checked'
				},
				{
					value: '2',
					name: '个人',
				},
			],
			wulala:'',
			current: 0,
			shopList: '',  //商品详情数据列表
			addressList: '',  //地址列表信息
			logwayList: '',  //物流方式列表信息
			subTotal: '',  //总价价格
		}
	},
	methods: {
		radioChange(evt) {
			console.log('asdf')
			for (let i = 0; i < this.items.length; i++) {
				if (this.items[i].value === evt.target.value) {
					this.current = i;
					if(this.items[i].value == 'no')
					{
						this.isActive = true;
						this.comActive=true;
					}else{
						this.isActive = false;
						console.log("11111111111");
							if(this.items[i].value === 'specialPurpose'){
								this.invoices = [{
										value: '1',
										name: '企业',
									}
								];
							}else if(this.items[i].value === 'ordinary'){
								this.invoices = [{
										value: '1',
										name: '企业',
									},
									{
										value: '2',
										name: '个人',
									},
								]
							}
					}
					break;
				}
			}
		},
		redioList(evt){ //发票内容事件
			this.comActive = false;
			if(evt.target.value == '1'){
				this.wulala=[{
						id:'01',
						name:'公司名称'
					},{
						id:'02',
						name:'纳税人识别号'	
					},{
						id:'03',
						name:'地址,电话'	
					},{
						id:'04',
						name:'开户号及账号'	
				}]
			}else if(evt.target.value == '2'){
				this.wulala=[{
					id:'person',
					name:'名称'
				}]
			}
		},
		// 点击选择 配送方式
		deliveryClick(evt){
			this.delivery=true;
		},
		// 配送方式的 取消 按钮
		deliveryPopup(evt){
			this.delivery=false;
		},
		// 点击选择 支付方式
		checkClick(evt){
			this.payment=true;
		},
		// 支付方式页面的 取消 按钮
		checkPopup(evt){
			this.payment=false;
		},
		// 点击地址
		addressClick(evt){
			console.log('哈哈')
			this.addressActive = true;  //地址页面显示
			this.addressInfo = false;  //订单详细信息页面隐藏
		},
		// 地址详情的 取消 按钮
		cancelBtn(e){
			this.addressActive = false;  //地址页面隐藏
			this.addressInfo = true;  //订单详细信息页面显示
		},
		// 地址详情的 确定 按钮
		determineBtn(e){
			this.addressActive = false;  //地址页面隐藏
			this.addressInfo = true;  //订单详细信息页面显示
		},
		// 点击地址列表切换
		addTap(index){
			this.addcheckNum = index
		}
	},
	onLoad(options) {
		if (options.l == 2){
			uni.request({
				url: 'http://192.168.199.194/mobile/order/conOrder',
				method: 'GET',
				data: {
					Ident_Signboard: options.Ident_Signboard,  //令牌
					Ident_Signguid: options.Ident_Signguid,  //用户id
					long: options.long, //高
					kuan: options.kuan,  //宽
					clid: options.clid,  //材料id
					qty: options.qty,  //数量
					total: options.total,  //总价
					price: options.price,  //单价
					gyid: options.gyid,  //工艺id 
					gyname: options.gyname,  //工艺名称
					spid: options.spid , //商品序号
					l: 2,  //标识：区分是购物车发起的支付还是下单页直接过去的
				},
				success: (res) => {
					if(res.data.status == 0){
						this.subTotal = res.data.total;  //给subTotal（总价）赋值
						this.shopList = res.data.shop;  //给shopList（商品信息列表）赋值
						this.addressList = res.data.address;  //给addressList（地址列表信息）赋值
						this.logwayList = res.data.logway;  //给logwayList（物流方式列表信息）赋值
						console.log(res)
						console.log(this.shopList)
					}
				}
			})
		}
		
	}
}
